import {stripLetters} from './utils';

export function isMaskOptionTurnedOn(componentData) {
  const options = componentData.options;
  return typeof options !== 'undefined' && typeof options.mask !== 'undefined' && options.mask === true;
}

export function getValueForComponentEvent(event, componentData) {
  return event.trigger === 'showError' && isMaskOptionTurnedOn(componentData) && typeof componentData.storedValue !== 'undefined' ? componentData.storedValue : stripLetters(event.target.value);
}

function isLast4Digits(value, index) {
  return index >= (value.length - 4);
}

function isWhiteSpace(value) {
  return value.replace(/\s/g, '').length !== value.length;
}

export function maskCreditCard(value) {
  return value.split('').map((char, index) => {
    if (isLast4Digits(value, index)) {
      return char;
    } else if (isWhiteSpace(char)) {
      return ' ';
    } else {
      return '*';
    }
  }).join('');
}