import './cc-cvv.html';
import '../styles/defaults.css';
import postRobot from '../../../post-robot-wrapper';
import {handleEvent, handleReturnKeyEvent, runEventOnElement} from '../input-events';
import {applyOptions} from '../options';
import {getComponentIdFromQueryString, getControllerIdFromQueryString} from '../querystring';
import {checkToSendEvent, stripLetters} from '../utils.js';
import {config} from '../config';
import {
  addGetComponentDataByValue,
  addHandleOptions,
  addTriggerEvent,
  mountComponent,
  saveCreditCardNumberData
} from '../payment-events';
import {generateComponentData} from '../payment-component-data';
import {csvLength} from '../validator';
import {getLocaleMessage} from '../localization/localized-messages';
import {createEvent} from '../cross-browser-support';
import {getValueForComponentEvent, isMaskOptionTurnedOn} from '../masking';
import {setAriaLabel, setLabelText} from '../accessibility';

const COMPONENT_TYPE = 'cardcvv';

let componentData = generateComponentData(COMPONENT_TYPE, getComponentIdFromQueryString(COMPONENT_TYPE),getControllerIdFromQueryString());

const defaultOptions = {
  placeholderText: getLocaleMessage('en-US', 'cardSecurityCode')
};

const controllerListener = postRobot.listener({ window: componentData.controller.window, domain: config.domain });
const controllerEmitter = postRobot.client({ window: componentData.controller.window, domain: config.domain });

const triggerData = {
  componentData: componentData,
  getElement: getElement
};

mountComponent(controllerEmitter, componentData, handleOptions, emitComponentReady);
addHandleOptions(controllerListener, handleOptions);
addGetComponentDataByValue(controllerListener, getValue);
addTriggerEvent(controllerListener, runEventOnElement, triggerData);
saveCreditCardNumberData(controllerListener, saveCreditCardData);

export function getValue() {
  if (isMaskOptionTurnedOn(componentData)) {
    return componentData.storedValue;
  } else {
    return getElement().value;
  }
}

/**
 * getElement returns the ccCVV dom element
 * @returns {HTMLElement}
 */
export function getElement() {
  return document.getElementById('ccCVV');
}

/**
 * getLabelElement returns the label dom element
 * @returns {HTMLElement}
 */
export function getLabelElement() {
  return document.getElementById('ccCVV-label');
}

/**
 * saveCreditCardData saves updated data to componentData
 * @param {object} updatedCreditCardData
 */
export function saveCreditCardData(updatedCreditCardData) {

  const currentBrand = componentData.creditCardData.brand;
  componentData.creditCardData = updatedCreditCardData;

  limitFieldLength();

  if (updatedCreditCardData.brand !== currentBrand && updatedCreditCardData.brand !== 'UNKNOWN') {
    if (getElement().value.length !== 0) {
      const onChangeEvent = createEvent('input');
      onChangeEvent.trigger = 'showError';
      getElement().dispatchEvent(onChangeEvent);
    }
  }

}

/**
 * getComponentData returns componentData object
 * @returns {{componentType: string, prevState: {}, controller: {id: (string|*)}, componentId: (string|*), creditCardData: {}}}
 */
export function getComponentData() {
  return componentData;
}

/**
 * handleOptions applies options to the ccCVV dom element
 * @param {object} data
 */
export function handleOptions(data) {
  let el = getElement();

  defaultOptions.placeholderText = getLocaleMessage(data.instanceOptions.locale, 'cardSecurityCode');
  applyOptions(el, data.options, defaultOptions);
  addInstanceOptions(data.instanceOptions);
  componentData.options = data.options;
  setAriaLabel(el, data.instanceOptions.locale, COMPONENT_TYPE);
  setLabelText(getLabelElement(), data.instanceOptions.locale, COMPONENT_TYPE);
  document.querySelector('html').setAttribute('lang', data.instanceOptions.locale);
}

/**
 * Stores the instance options in the component state
 * @param instanceOptions
 */
export function addInstanceOptions(instanceOptions) {
  componentData.instanceOptions = instanceOptions;
}

/**
 * limitFieldLength sets the cvv field maxLength appropriately for the card brand
 */
export function limitFieldLength() {
  const maxLength = csvLength(componentData.creditCardData.brand);
  const el = getElement();
  el.maxLength = maxLength;
  if (el.value.length > maxLength) {
    el.value = el.value.substring(0, maxLength);
  }
  if (isMaskOptionTurnedOn(componentData) && componentData.storedValue > maxLength) {
    componentData.storedValue = componentData.storedValue.substring(0, maxLength);
  }
}

/**
 * handleChange accepts a change event, strips letters from the field value,
 * limits the length of the value, and calls handleEvent
 * @param {Event} event
 */
export function handleChange(event) {
  let el = getElement();
  let hasFocus = document.activeElement === el;
  const initialEventValue = event.target.value;

  if (event.trigger !== 'showError') {
    event.target.value = stripLetters(initialEventValue);
  }


  if(event.trigger === 'clear') {
    componentData.prevState = {
      empty: false,
      complete: false,
      error: null
    };
    componentData.storedValue = '';
    handleEvent(componentData, 'change', event, getValueForComponentEvent(event, componentData));
  } else if (checkToSendEvent(initialEventValue, event) && (hasFocus || event.trigger === 'showError')) {
    limitFieldLength();
    if(componentData.creditCardData?.brand && event.target.value.length !== csvLength(componentData.creditCardData.brand)) {
      event.trigger === 'showError';
    }
    handleEvent(componentData, 'change', event, getValueForComponentEvent(event, componentData));
  }

}

/**
 * handleBlur accepts a blur event and sends it on to handleEvent
 * @param {Event} event
 */
export function handleBlur(event) {
  handleEvent(componentData, 'blur', event);
  if (isMaskOptionTurnedOn(componentData)) {
    componentData.storedValue = event.target.value;
    if(!componentData.prevAutofill) {
      handleAutofill(event);
    }
    event.target.value = event.target.value.replace(/\d/g, '*');
  }

}

/**
 * handleFocus accepts a focus event and sends it on to handleEvent
 * @param {Event} event
 */
export function handleFocus(event) {
  handleEvent(componentData, 'focus', event);

  if (isMaskOptionTurnedOn(componentData) && typeof componentData.storedValue !== 'undefined') {
    event.target.value = componentData.storedValue;
  }
}

/**
 * handleAutofill accepts an autofill event and sends it on to handleEvent
 * @param {Event} event
 */
export function handleAutofill(event) {
  componentData.prevAutofill = true;
  handleEvent(componentData, 'autofill', event);
}

/**
 * emitComponentReady calls handleEvent with 'ready'
 */
export function emitComponentReady() {
  handleEvent(componentData, 'ready');
}

export function addInputHtmlToDom() {
  document.body.innerHTML += inputHtml();
}

export function keyUpEvent(event) {
  handleReturnKeyEvent(event, componentData);
}

export function inputHtml() {
  return `<label id="ccCVV-label" for="ccCVV" class="sr-only"></label>
        <input id="ccCVV"
         name="ccCVV"
         autocomplete="cc-csc"
         class="base empty"
         type="tel"
         onfocus="handleFocus(event)"
         onblur="handleBlur(event)"
         oninput="handleChange(event)"
         onkeyup="keyUpEvent(event)"
         minLength="3"
         maxlength="4"
         value=""
         aria-invalid="false"
         aria-errormessage="ccCVV-error"
  /><div id="ccCVV-error" class="sr-only" aria-live="assertive"></div>`;
}
